import { ColumnDef, Row } from '@tanstack/react-table';
import EditIcon from '@/components/icons/EditIcon';
import { useNavigate } from 'react-router-dom';
import { MaintenanceCenter } from '@/types/maintenanceCenter';
import truncateText from '@/utils/truncateText';
// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<MaintenanceCenter> }) => {
    const navigate = useNavigate();

    return (
        <div className="flex items-center gap-7">
            <button
                onClick={() => navigate(`/maintenance/centers/${row.original.id}/edit`)}
                className="text-primary hover:text-primary/80"
            >
                <EditIcon className="text-primery-blue-400" />
            </button>
        </div>
    );
};

export const columns: ColumnDef<MaintenanceCenter>[] = [
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    مركز الصيانة
                </div>
            );
        },
        cell: ({ row }) => (
            <div>
                <div className="font-medium text-sm tracking-normal text-start text-primery-blue-900">
                    {row.original.name}
                </div>
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    حالة التعامل
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="p-1 w-fit bg-[#22AD5C] rounded text-xs text-white-blue-50 font-medium tracking-normal leading-[13.8px]">
                    {row.original.status}
                </div>
            );
        },
    },
    {
        accessorKey: 'date',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    المنطقة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.location}
                </div>
            );
        },
    },
    {
        accessorKey: 'service',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    العنوان
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.description ? truncateText(row.original.description) : ''}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الخدمات
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    <div>{row.original.features ? truncateText(row.original.features.join(' ')) : ''}</div>{' '}
                </div>
            );
        },
    },
    {
        id: 'actions',
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
