import SettingsIcon from '@/components/icons/statistics/SettingIcon';
import CalendarIcon from '@/components/icons/statistics/CalendarIcon';
import { Card } from '@/types/statistics';
import StatisticsCards from '@/components/cards/StatisticsCards';
import ShopIcon from '@/components/icons/ShopIcon';
import MoneyIcon from '@/components/icons/MoneyIcon';
import MaintenanceCentersList from '../components/maintenanceCenters/MaintenanceCentersList';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { CenterDashboardCard } from '../types/centersDashboard.types';

const MaintenanceCenters = () => {
    const [stats, setStats] = useState<CenterDashboardCard | null>(null);

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({ endpoint: endpoints.maintenanCenter.dashboard });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, []);

    const cards: Card[] = [
        { text: 'عدد مراكز الصيانة', icon: <ShopIcon />, num: stats?.centers_count, percent: 0 },
        {
            text: 'متوسط الزيارات الشهرية',
            icon: <CalendarIcon />,
            num: stats?.average_monthly_maintenances,
            percent: 0,
        },
        {
            text: 'جاري الصيانة',
            icon: <SettingsIcon />,
            num: stats?.in_progress_maintenances_count,
            percent: 0,
            negative: true,
        },
        { text: 'إجمالي تكاليف الصيانة', icon: <MoneyIcon />, num: stats?.total_cost, label: ' ر.س', percent: 0 },
    ];
    
    return (
        <div className="w-full mt-6">
            <StatisticsCards cards={cards} />
            <div className="mt-6">
                <MaintenanceCentersList />
            </div>
        </div>
    );
};

export default MaintenanceCenters;
